	module.exports = {"data":{"site":{"siteMetadata":{"title":"Nobody Listens Anyway","author":"Justin Ball"}},"markdownRemark":{"id":"/Users/jbasdf/projects/justinball/src/posts/2006-12-05-something-scrumdidilyumptious/index.md absPath of file >>> MarkdownRemark","html":"<p>Just short of a year ago I hacked together a project that we loving called <a href=\"http://scrumdidilyumptio.us/\">scrumdidilyumptio.us</a>. I and Wiley managed to finish it at about 2 in the morning in a hotel in NY during the Mellon meeting. (I actually worked on it during the meeting which resulted in terrible embarsement when my system speaker went off for a few minutes during a talk from MIT because of a misstep in setting up my database.) At any rate <a href=\"http://opencontent.org/blog/archives/289\">Wiley finally decided to say something about it which is important because people actually read his blog</a>. He has a sweet idea that integrates with 43things. I hope to spend some time working on scrumdidilyumptious when I get back from the Identity Conference. We should be able to get a new release out in a few weeks.</p>","frontmatter":{"title":"Something Scrumdidilyumptious","date":"December 05, 2006","image":null}}},"pathContext":{"path":"/something-scrumdidilyumptious"}}

